import * as React from 'react'

const CorporateSponsorshipLevel = ({ title, price, benefits, url, available=true }) => (
  <div className='border shadow-sm flex flex-col items-stretch justify-stretch gap-0 w-full'>
    <h4 className='bg-zinc-500 text-white p-4 uppercase font-semibold tracking-tight leading-none text-xl'>{title}</h4>
    <p className='px-4 py-2 leading-none bg-zinc-100 text-lg font-light'>Price: ${price.toLocaleString()}</p>
    <ul className='list-disc mt-2 mb-4 me-4 ms-8 text-sm'>
      { benefits.map((benefit, index) => (
        <li className='font-light mt-2' dangerouslySetInnerHTML={{ __html: benefit }}></li>
      )) }
    </ul>
    <div className='px-4 py-2 text-xs bg-zinc-100 mt-auto'>
      { available && <a className='inline-block bg-[var(--sro-blue)] hover:bg-[var(--sro-blue-dark)] rounded-full px-4 py-2 text-center text-white font-semibold uppercase' href={url} target='_blank' rel='noreferrer'>Purchase</a> }
      { !available && <span className='inline-block cursor-not-allowed bg-zinc-200 rounded-full px-4 py-2 text-center text-zinc-400 font-semibold uppercase'>Sold Out</span> }
    </div>
  </div>
)

export default CorporateSponsorshipLevel