import * as React from 'react'
import { Link } from "gatsby"

const PrimaryNavLink = ({ text, url }) => (
  <li className="w-full md:w-auto">
    <Link to={url} className="block py-4 text-center uppercase font-light text-xl tracking-wider max-sm:hover:bg-[var(--sro-blue-darker)]">{text}</Link>
  </li>
)

export default PrimaryNavLink