import * as React from 'react'

const CDNImage = ({ url, alt, cssClass, release = '' }) => {

  const baseURL = process.env.NODE_ENV === 'development' ? 'http://localhost:8888' : `https://cdn.jsdelivr.net/gh/standingroom/assets${release}`

  return ( <img className={cssClass} alt={alt} src={baseURL + url} /> )

}

export default CDNImage